body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fullSection {
  width: 100vw;
  height: 100vh; }

.introColor {
  background-color: #282c34; }

.colorOne {
  background-color: #282c34; }

.flex {
  display: flex; }

.video-wrapper {
  width: 50%;
  max-width: 800px;
  height: 80%;
  max-height: 800px; }

.centeredSection {
  display: flex;
  align-items: center;
  justify-content: center; }

.rowDisplay {
  flex-direction: row; }

.columnDisplay {
  flex-direction: column; }

.filler {
  flex: 1 1 auto; }

.rippleBody {
  width: 100%;
  height: 100%; }

.item {
  padding: 50px;
  padding-bottom: 10px; }

.vertical {
  width: 25%;
  min-width: 300px;
  height: auto; }

.quoteCredit {
  padding-left: 50px;
  font-size: 1.5em;
  font-family: 'Lato'; }

.headline {
  width: 100%;
  height: auto;
  padding: 25px;
  padding-left: 0px; }

div, h1, p {
  font-family: 'Merriweather', sans-serif;
  font-weight: 100;
  color: white; }

h1 {
  font-size: 5em;
  margin: 0; }

#credits {
  font-size: 3em;
  margin: 0;
  font-family: 'Lato'; }

p {
  font-size: 3em;
  margin: 0; }

.main-quote {
  font-size: 4em;
  max-width: 50%;
  font-family: 'Merriweather', sans-serif; }

